.center {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

mjx-container {
  font-size: 120% !important;
}

.nodeLabel mjx-container {
  font-size: 140% !important
}

.small mjx-container {
  font-size: 110% !important
}

.mermaid {
  text-align: center;
  display: block !important;
  background: white !important;
}

.mermaid polygon {
  fill: transparent !important;
  stroke: transparent !important;
}

.mermaid circle {
  fill: transparent !important;
  stroke: black !important;
  stroke-width: 2px !important;
}

.mermaid rect {
  fill: transparent !important;
  stroke: black !important;
  stroke-width: 2px !important;
}

.mermaid path {
  stroke: black !important;
}

.mermaid .marker {
  fill: black !important;
}

.mermaid .edgeLabel {
  background: white !important;
}

.mermaid span {
  color: black !important;
}

.mermaid .selected {

  circle,
  rect {
    fill: #F5C2C1 !important;
  }
}

#error {
  color: red;
}

#form {
  font-family: monospace;

  #hidden-wrapper {
    position: absolute;
    visibility: hidden;
  }

  #hidden-input,
  #word-input {
    width: 50%;
    height: 40px;
  }

  #hidden-input,
  #word-input,
  #word {
    color: black;
    font-size: 1rem;
    border: 3px solid black;
    margin: 0 auto;
    padding: 8px 12px;
    border-radius: 4px;
    display: block;
    border-radius: 4px;
    resize: none;
    overflow: hidden;
  }

  #word {
    display: none;

    p {
      margin: 0;
      float: left;

      &.next {
        margin: -1px;
        border: 1px solid red;
      }

      &.next,
      &.remain {
        color: gray;
      }
    }
  }

  .line {
    display: flex;
    width: 50%;
    margin: 10px auto;

    p {
      margin: 0;
      padding: 0.25rem;
      line-height: 1.5rem;
    }

    #word {
      display: flex;

      p.active {
        color: black;
        border: 1px solid black;
      }

      p {
        margin: 0;
        padding: 0.25rem;
        border: 1px solid lightgray;
        color: lightgray;
      }
    }

    #result {
      font-weight: bold;

      &.pass {
        color: green;
      }

      &.fail {
        color: red;
      }
    }
  }

  #buttons {
    margin: 0 auto;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  svg {
    margin-right: 1.5rem;
  }

  .buton {
    position: relative;
    border: 0;
    font: 300 14px;
    color: #FFFFFF;
    padding: 10px 15px;
    border-radius: 3px;
    display: inline-block;
    margin: 5px;
    cursor: pointer;
    overflow: hidden;
    outline: none;
  }

  .buton[disabled] {
    background: #ddd;
    pointer-events: none;
  }

  .buton>* {
    position: relative;
    z-index: 5;
  }

  .buton:hover:after {
    left: 0;
  }

  .buton:after {
    top: 0;
    left: -100%;
  }

  .buton-blue {
    background: -webkit-linear-gradient(left, #2a83be 40px, #3498db 0);
  }

  .buton-blue:after {
    background-color: #2a83be;
  }

  .buton-red {
    background: -webkit-linear-gradient(left, #c0392b 40px, #e74c3c 0);
  }

  .buton-red:after {
    background-color: #c0392b;
  }

  .buton-green {
    background: -webkit-linear-gradient(left, #27ae60 40px, #2ecc71 0);
  }

  .buton-green:after {
    background-color: #27ae60;
  }
}

#viewer {
  width: 800px;
}

.CodeMirror {
  margin-top: 10px;
  border: 1px solid #ccc;
  border-radius: 8px;
  height: 300px;
}

#refresh {
  outline: none;
  cursor: pointer;
  margin: 0;
  border: 0;
  height: 35px;
  width: 35px;
  color: #ffffff;
  background-color: #2a408e;
  transition: background-color 0.2s ease-in-out 0s, opacity 0.2s ease-in-out 0s;
  border-radius: 10px;
}

#refresh:hover {
  color: #ffffff;
  background-color: #1a2757;
}

.dropdown {
  position: relative;
  margin-right: 15px;
  font-family: sans-serif;
  font-size: 15px;
  display: inline-block;
  height: 35px;
  width: 150px;
  line-height: 35px;
  padding: 0;
  background: #1c2028;
  text-align: left;
  color: #fff;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  border-radius: 10px;
}

.arrow {
  margin: 0 10px 0 10px;
  float: right;
  rotate: 180deg;
  transition: all 0.3s;
}

.arrow::before {
  line-height: 35px;
  font-size: 20px;
}

.dropdown .selected {
  margin-left: 20px;
}

.dropdown::before {
  position: absolute;
  top: 50%;
  right: 16px;
  border-width: 6px 6px 0 6px;
  border-style: solid;
  border-color: #fff transparent;
}

.rotated {
  transform: rotate(-180deg);
}

.dropdown ul {
  position: absolute;
  top: 120%;
  right: 0;
  left: 0;
  margin: 0;
  padding: 0;
  list-style: none;
  z-index: 99;
  border-radius: 10px;
  box-shadow: inherit;
  background: inherit;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  opacity: 0;
  visibility: hidden;
}

.dropdown li {
  padding: 0 15px;
  height: 35px;
  line-height: 35px;
  overflow: hidden;
  font-family: sans-serif;
  font-size: 15px;
  color: #ffffff;
}

.dropdown li:last-child {
  border-bottom: none;
}

.dropdown li:hover {
  background-color: rgb(35, 40, 49);
  border-radius: 10px;
}

.dropdown.active ul {
  opacity: 1;
  visibility: visible;
  border-radius: 10px;
  height: auto;
  max-height: 200px;
  overflow-x: hidden;
}
